import React from 'react';
import {connectToStore} from 'tools/connect';
import {withFocusable, FocusableProps} from '@noriginmedia/react-spatial-navigation';
import {StoreType} from 'stores/types';
import {Link} from 'react-router-dom';
import Typography from 'components/Typography';

import withBem from 'hoc/withBem';
import {routes} from 'stores/ui';
import './style.scss';

interface AddressProps {
    isShowAddress?: boolean;
}

const Address: React.FC<AddressProps & StoreType & BemProps & FocusableProps> = ({user, bem, isShowAddress = true, focused}) => {
    return (
        <Link to={routes.MAP}>
            <div className={bem.modify({focused})}>
                <div className={bem('icon')} />
                {isShowAddress && (
                    <div className={bem('block')}>
                        <div>
                            <Typography type="subheader">{user.address}</Typography>
                        </div>
                        <div>
                            <Typography type="subheaderDescription">{user.location || 'Уточнить адрес'}</Typography>
                        </div>
                    </div>
                )}
            </div>
        </Link>
    );
};

export default withFocusable()(withBem(connectToStore(Address), 'Address'));
