import React from 'react';

import './style.scss';

type SizeType = 's' | 'm' | 'l' | 'xl';

const SIZES: SizeType[] = ['s', 'm', 'l', 'xl'];

interface CellProps {
    className?: string;
    verticalPadding?: number | string;
    topPadding?: number | string;
    bottomPadding?: number | string;
    s?: number | string;
    m?: number | string;
    l?: number | string;
    xl?: number | string;
    [key: string]: any;
}

const Col: React.FC<CellProps> = (props) => {
    const {children, className = '', verticalPadding, topPadding, bottomPadding, style = {}} = props;

    const sizeClasses = SIZES.map((size: SizeType) => {
        const sizeProp = props[size];
        return sizeProp ? `sb-Col${size === 's' ? '' : '-' + size}-${sizeProp}` : '';
    }).join(' ');

    const hiddenClasses = SIZES.map((size) => {
        const sizeProp = props[`${size}Hidden`];
        return sizeProp ? `sb-Col-${size}-hidden` : '';
    }).join(' ');

    const autoClasses = SIZES.map((size) => {
        const sizeProp = props[`${size}Auto`];
        return sizeProp ? `sb-Col${size === 's' ? '' : '-' + size}-auto` : '';
    }).join(' ');

    const offsetClasses = SIZES.map((size) => {
        const sizeProp = props[`${size}Offset`];
        return sizeProp ? `sb-Col-offset${size === 's' ? '' : '-' + size}-${sizeProp}` : '';
    }).join(' ');

    const styles = {
        ...style,
        paddingTop: `${verticalPadding || topPadding}px`,
        paddingBottom: `${verticalPadding || bottomPadding}px`,
    };

    return (
        <React.Fragment>
            <div style={styles} className={`sb-Col ${sizeClasses} ${hiddenClasses} ${autoClasses} ${offsetClasses}`}>
                <div className={`sb-Col__content ${className}`}>
                    {children}
                </div>
                <div className="sb-Col__gutter" />
            </div>
            
        </React.Fragment>
    );
};

export default Col;
