import React from 'react';
import {StoreType} from 'stores/types';

import Typography from 'components/Typography';
import Row from 'components/Grids/Row';
import Col from 'components/Grids/Col';
import Container from 'components/Grids/Container';

import withBem from 'hoc/withBem';
import {connectToStore} from 'tools/connect';

import './style.scss';

interface NavigationProps {
    title: string;
    subtitle?: string;
    back?: string;
    rightElement: React.ReactNode;
}

const Navigation: React.FC<StoreType & NavigationProps & BemProps> = ({bem, ui, back, title, subtitle, rightElement}) => {
    const goBack = () => back && ui.gotoPage(back);

    return (
        <Container>
            <Row>
                <Col s={4} m={6} l={8} xl={12}>
                    <nav className={bem()}>
                        {back && <span className={bem('back')} onClick={goBack} />}
                        <section className={bem('defineBlock')}>
                            <div className={bem('logo')} />
                            <div className={bem('defineData')}>
                                <div>
                                    <Typography type={subtitle ? 'subheader' : 'header'}>{title}</Typography>
                                </div>
                                {subtitle && (
                                    <div>
                                        <Typography type="subheaderDescription">{subtitle}</Typography>
                                    </div>
                                )}
                            </div>
                        </section>

                        <section className={bem('defineBlock')}>
                            {rightElement}
                        </section>
                    </nav>
                </Col>
            </Row>
        </Container>
    );
};

export default connectToStore(withBem(Navigation, 'Navigation'));
